<template>
<div class="main-div">
  <div id="mainTextDiv" class="main-text-div">
    <h1 class="main-title">C:\webpage</h1>
  </div>
  <div class="cable-div">
    <cables/>
  </div>
  <div class="mobile-menu">
    <p class="mobile-menu-subtitle" @click="mobileMenuVisibleToggle">Menu</p>
    <div id="mobileMenuSelection" class="mobile-menu-content">
      <p class="mobile-menu-subtitle" @click="changePage('/projects')">Projects</p>
      <p class="mobile-menu-subtitle" @click="changePage('/about')">About Me</p>
      <p class="mobile-menu-subtitle" @click="changePage('/contact')">Contact</p>
    </div>
  </div>
</div>
</template>

<script>
import cables from '../components/mainPage/cables.vue'

export default {
  name: 'mainPage',
  components: {
    cables
  },
  methods: {
    mobileMenuVisibleToggle() {
      document.getElementById("mobileMenuSelection").classList.toggle("mobile-menu-content-visible");
    },
    changePage(newPage) {
      this.$router.push(newPage);
    }
  }
}
</script>

<style>
.main-div {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 1.5em;
}
.mobile-menu-subtitle {
  color: white;
  font-size: 2em;
  font-family: 'Roboto Mono', sans-serif;
  background: gray;
  padding: 20px;
  border-radius: 10px;
  transition: all 0.25s;
}
.mobile-menu-subtitle:active {
  transform: translateY(+10px);
}
.mobile-menu {
  display: none;
}
@media screen and (max-width: 700px) {
  .cable-div {
    display: none;
  }
  .main-div {
    font-size: 0.8em;
  }
  .mobile-menu {
    display: block;
  }
  .main-title {
    font-size: 50px;
  }
}

.mobile-menu-content {
  max-height: 0;
  transition: max-height 0.3s ease-out;
  overflow: hidden;
  background: rgb(61, 61, 61);
  padding: 10px;
  border-radius: 10px;
}
.mobile-menu-content-visible {
  max-height: 500px;
  transition: max-height 0.8s ease-in;
}
</style>